import { useCallback } from "react";
import { Button, Flex, Image, Text, useToast } from "@chakra-ui/react";
import Timer from "./Timer";
import { useMoralis } from "react-moralis";
import ProgressBar2 from "./ProgressBar2";
import ConnectWalletDrawer from "./ConnectWalletDrawer";
import BuyToken from "./BuyToken";
import iconHelp from "../assets/iconHelp.svg";

function Hero({ login }) {
  /**
   * TOAST : START
   */
  const toast = useToast();
  const showErrToast = useCallback(
    (errTitle, errMsg) => {
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        title: errTitle,
        description: errMsg ? errMsg : "",
        status: "warning",
        duration: 3000,
        isClosable: true,
      });
    },
    [toast]
  );
  const showInfoToast = useCallback(
    (errTitle, errMsg) => {
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        title: errTitle,
        description: errMsg ? errMsg : "",
        status: "info",
        duration: 60000,
        isClosable: true,
      });
    },
    [toast]
  );
  const showSuccessToast = useCallback(
    (errTitle, errMsg) => {
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        title: errTitle,
        description: errMsg ? errMsg : "",
        status: "success",
        duration: 60000,
        isClosable: true,
      });
    },
    [toast]
  );
  /**
   * TOAST : END
   */

  const { isAuthenticated } = useMoralis();
  return (
    <>
      <Flex
        direction={"column"}
        color="#DDCDEA"
        justifyContent="center"
        border="4px solid rgba(255, 255, 255, 0.2)"
        borderRadius={"16px"}
        background="linear-gradient(90deg, rgba(52, 18, 64, 0.5) 0%, rgba(0, 31, 78, 0.5) 51.04%, rgba(35, 20, 68, 0.5) 100%)"
        padding={"2rem .5rem"}
        backdropFilter="blur(5px)"
      >
        <Text
          as="h1"
          textTransform={"uppercase"}
          fontWeight="black"
          fontSize={"2rem"}
          fontFamily={"Russo One"}
        >
          Buy $BIGNGT on presale
        </Text>
        <Text as="h2" textTransform={"uppercase"} fontSize={"1.2rem"}>
          1 $BIGNFTS = $1
        </Text>
        <ProgressBar2></ProgressBar2>
        {/* <Box background={"#140421"}>
          <Text
            textTransform={"uppercase"}
            fontWeight="black"
            fontSize={"1.2rem"}
            letterSpacing="2rem"
            padding={"0.25rem 1rem"}
            background="-webkit-linear-gradient(0deg, #5CA1FF, #D48EFF)"
            style={{
              "-webkit-background-clip": "text",
              "-webkit-text-fill-color": "transparent",
            }}
          >
            Presale ENDS In
          </Text>
        </Box> */}
        {!isAuthenticated ? (
          <Flex gap={"1rem"} margin=".5rem auto" marginBottom={"1rem"}>
            <ConnectWalletDrawer login={login} />

            <Button
              textTransform="uppercase"
              fontWeight={"black"}
              borderRadius="4px"
              boxShadow={"2px 2px 23px rgba(0, 0, 0, 0.3)"}
              padding=".75rem 1.5rem"
            >
              <Image src={iconHelp} marginRight=".5rem" />
              How to Buy
            </Button>
          </Flex>
        ) : (
          ""
        )}
        <Timer />
        {isAuthenticated ? (
          <BuyToken toast={toast} showErrToast={showErrToast} />
        ) : (
          ""
        )}
      </Flex>
    </>
  );
}

export default Hero;
