/* eslint-disable no-unused-vars */
import { Flex, Heading, Text } from "@chakra-ui/react";
import BuyButton from "./BuyButton";
import CoinSelector from "./CoinSelector";
import InputAmount from "./InputAmount";
import { useState, useCallback } from "react";
import {
  useMoralis,
  useERC20Balances,
  useNativeBalance,
  useChain,
} from "react-moralis";

function BuyToken({ toast, showErrToast }) {
  const [buyingCoin, setBuyingCoin] = useState("IBAT");
  const [buyingCoinAmount, setBuyingCoinAmount] = useState("IBAT");
  const [buyingCoinBalance, setBuyingCoinBalance] = useState(0);

  /**
   * Moralis : START
   */
  const { isAuthenticated, isWeb3Enabled, user } = useMoralis();
  const {
    fetchERC20Balances,
    data: allBalances,
    isLoading: isLoadingERC20Balances,
    isFetching: isFetchingERC20Balances,
    error,
  } = useERC20Balances(); // Fetch ERC20Balances

  const { chainId } = useChain(); // Fetch current chainId
  const {
    getBalances,
    data: nativeBalance,
    nativeToken,
    error: nativeBal_error,
    isLoading: nativeBal_isLoading,
  } = useNativeBalance({ chain: chainId }); // Fetch Native Balance
  /**
   * Moralis : END
   */

  const setNativeValue = useCallback((element, value) => {
    const valueSetter = Object.getOwnPropertyDescriptor(element, "value").set;
    const prototype = Object.getPrototypeOf(element);
    const prototypeValueSetter = Object.getOwnPropertyDescriptor(
      prototype,
      "value"
    ).set;

    if (valueSetter && valueSetter !== prototypeValueSetter) {
      prototypeValueSetter.call(element, value);
    } else {
      valueSetter.call(element, value);
    }
  }, []);

  const getTokenBalance = useCallback(
    (token) => {
      let tempCoin = 0;
      if (!isAuthenticated) {
        console.error(`dev-not authenticated`);
        return;
      }
      if (token.toUpperCase() === "BNB") {
        return nativeBalance.formatted;
      } else {
        tempCoin = allBalances.filter((c) => {
          let symbol = c.symbol + "";
          return symbol.toUpperCase() === token.toUpperCase();
        });
        console.log(`getting bal of - ${token}`);
        tempCoin = tempCoin && tempCoin[0] ? tempCoin[0] : null;
        tempCoin = tempCoin
          ? tempCoin.balance / Math.pow(10, tempCoin.decimals)
          : 0;
      }
      tempCoin = Number(tempCoin).toFixed(3);
      return parseFloat(tempCoin);
    },
    [allBalances, isAuthenticated, nativeBalance.formatted]
  );

  const setMaxAmount = useCallback(
    (elID) => {
      // deducting 1IBAT from overall amount so that total no. of holders doesn't decrease coz they're staking MAX AMOUNT - making IBAT Holding = 0 IBAT
      const TO_MAINTAIN_WALLET_HOLDERS = 1;

      let e = 0;
      try {
        e = getTokenBalance(buyingCoin);
        if (buyingCoin.toUpperCase() === "IBAT")
          e -= TO_MAINTAIN_WALLET_HOLDERS;
      } catch (error) {
        console.error(error);
        console.error(`dev-setSwapAmount crashed `);
      }
      if (e && e <= 0) e = 0;

      let input = document.getElementById(elID);
      setNativeValue(input, e);
      input.dispatchEvent(new Event("input", { bubbles: true }));
    },
    [getTokenBalance, setNativeValue, buyingCoin]
  );

  return (
    <Flex
      width={"full"}
      justifyContent="space-between"
      gap={"1rem"}
      marginTop="2rem"
      padding={"1rem"}
    >
      <Flex
        width={"50%"}
        direction="column"
        alignItems={"flex-start"}
        gap=".5rem"
      >
        <Heading as="h3" fontSize={"1.5rem"} minWidth="max-content">
          Payment Method
        </Heading>
        <Flex
          direction={"column"}
          gap="1rem"
          maxWidth={"500px"}
          width="full"
          padding={"1rem .5rem"}
          borderRadius="4px"
          backdropFilter="blur(2px)"
        >
          <CoinSelector
            dropDownLabel={"Buy with"}
            coin={buyingCoin}
            setCoin={setBuyingCoin}
            coinBalance={buyingCoinBalance}
            isAuthenticated={isAuthenticated}
            isLoadingERC20Balances={isLoadingERC20Balances}
            allBalances={allBalances}
            nativeBalance={nativeBalance}
          />

          <InputAmount
            elID="buyingCoinAmount"
            setAmount={setBuyingCoinAmount}
          />
          <BuyButton
            coin={buyingCoin}
            bigNFTAmount={buyingCoinAmount}
            toast={toast}
            showErrToast={showErrToast}
          ></BuyButton>
        </Flex>
      </Flex>
      <Flex
        width={"50%"}
        direction="column"
        alignItems={"flex-start"}
        gap=".5rem"
      >
        <Heading as="h3" fontSize={"1.5rem"} minWidth="max-content">
          Token Details
        </Heading>
        <Flex
          background={
            "linear-gradient(90deg, rgba(23, 44, 102, 0.3) 0%, rgba(24, 22, 82, 0.3) 100%)"
          }
          borderRadius="4px"
          border={"2px solid rgba(255, 255, 255, 0.27)"}
          padding="1rem"
          width="full"
          direction={"column"}
          gap="1rem"
        >
          <Flex direction={"column"} alignItems="flex-start">
            <Text fontWeight={"bold"} fontSize=".85rem">
              Total Supply
            </Text>
            <Text>100,000,000</Text>
          </Flex>
          <Flex direction={"column"} alignItems="flex-start">
            <Text fontWeight={"bold"} fontSize=".85rem">
              Token Contract Address
            </Text>
            <Text overflowWrap={"anywhere"} textAlign="left">
              0x19cd9b8e42d4ef62c3ea124110d5cfd283ceac43
            </Text>
          </Flex>
          <Flex direction={"column"} alignItems="flex-start">
            <Text fontWeight={"bold"} fontSize=".85rem">
              Token Decimals
            </Text>
            <Text>2</Text>
          </Flex>
          <Flex direction={"column"} alignItems="flex-start">
            <Text fontWeight={"bold"} fontSize=".85rem">
              $BIGNFT Balance
            </Text>
            <Text>2</Text>
          </Flex>
        </Flex>
      </Flex>
    </Flex>
  );
}

export default BuyToken;
